import React from "react";
import Slider from "rc-slider";
import "rc-slider/assets/index.css";

import { useSliderPropsResponsive, useTimeRangeSelector } from "./hooks";
import toolsTimeTimeZoneConstants from "./constants";
import { E_TimeRangeSelectorTimeZoneListItemActions } from "./types";
import "./styles.scss";

function TimeZone() {
  const { state, eventHandlers } = useTimeRangeSelector();
  const sliderPropsResponsive = useSliderPropsResponsive(
    toolsTimeTimeZoneConstants.sliderStyleProps,
    state.is24HourFormat
  );
  const [dataOfMainTimeZone, ...dataOfOtherTimeZones] = state.timeZonesData;
  const [mainTimeZoneTimeRangeFrom, mainTimeZoneTimeRangeTo] =
    dataOfMainTimeZone.timeRange.timeValuesForDisplay;

  return (
    <div className="TimeZone h-screen container mx-auto w-full sm:w-11/12 md:w-10/12 lg:w-11/12 xl:w-10/12 2xl:w-8/12">
      <div className="grid grid-cols-1 gap-3 sm:gap-6 2xl:gap-8 mt-5 lg:mt-10">
        {/* Main timezone and slider */}
        <div className="bg-white shadow-lg bg-gradient-to-t from-neutral-100 px-5 pt-3 sm:px-8 sm:pt-6 md:px-10 md:pt-8 pb-7 sm:pb-11 flex flex-col justify-center select-none">
          <div className="flex flex-col sm:flex-row items-start sm:items-center gap-3 sm:gap-10">
            {/* Main timezone meta info */}
            <div className="flex flex-col">
              <span className="font-semibold text-lg sm:text-2xl font-sans text-gray-500">
                {dataOfMainTimeZone.countryName}
              </span>
              <div className="text-gray-500 tracking-wide flex flex-col">
                <span className="text-sm sm:text-base">{dataOfMainTimeZone.offsetNameLong}</span>
                <span className="font-mono text-xs sm:text-sm">{dataOfMainTimeZone.offsetNameShort}</span>
              </div>
            </div>
            {/* Main timezone selected time range */}
            <div className="my-2 m-auto sm:m-0 text-lg sm:text-3xl text-gray-600 tracking-wide">
              <span className="bg-emerald-100 p-3 rounded-xl shadow-lg">
                {mainTimeZoneTimeRangeFrom.hour}:
                {mainTimeZoneTimeRangeFrom.minute}&nbsp;
                <span className="text-xs sm:text-base opacity-70">
                  {mainTimeZoneTimeRangeFrom.meridiem}
                </span>
              </span>
              <span className="text-gray-400">&nbsp;&nbsp;–&nbsp;&nbsp;</span>
              <span className="bg-red-100 p-3 rounded-xl shadow-lg">
                {mainTimeZoneTimeRangeTo.hour}:{mainTimeZoneTimeRangeTo.minute}
                &nbsp;
                <span className="text-xs sm:text-base opacity-70">
                  {mainTimeZoneTimeRangeTo.meridiem}
                </span>
              </span>
            </div>
          </div>
          {/* Time range slider */}
          <div className="mt-7 sm:mt-10">
            <Slider
              range
              min={toolsTimeTimeZoneConstants.clockMarkDegreeRange.min}
              max={toolsTimeTimeZoneConstants.clockMarkDegreeRange.max}
              step={15}
              value={dataOfMainTimeZone.timeRange.degreeValues}
              pushable={15}
              onChange={eventHandlers.handleTimeRangeChange}
              draggableTrack
              {...sliderPropsResponsive}
            />
          </div>
        </div>
        {/* Other (favorite) timezones list */}
        <div className="m-auto w-11/12 sm:w-full grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-3 gap-5 xl:gap-7 px-5 py-3 md:p-4 rounded-lg">
          {dataOfOtherTimeZones.map((tzData) => {
            const [timeRangeFrom, timeRangeTo] =
              tzData.timeRange.timeValuesForDisplay;
            return (
              <div
                className="rounded-lg bg-white shadow-lg shadow-neutral-300"
                key={tzData.timeZoneName}
              >
                <div className="flex flex-col">
                  {/* Time Zone meta */}
                  <div className="px-5 py-3 sm:py-0 flex flex-col text-gray-500 tracking-wide sm:h-24 justify-center">
                    <div className="font-bold text-sm sm:text-base">
                      {tzData.countryName}
                    </div>
                    <div className="text-gray-500 sm:mt-1 flex flex-col">
                      <span className="text-xs sm:text-sm">{tzData.offsetNameLong}</span>
                      <span className="font-mono text-2xs sm:text-xs">{tzData.offsetNameShort}</span>
                    </div>
                  </div>
                  {/* Time range */}
                  <div className="-mb-2 py-4 sm:py-7 text-center text-md sm:text-xl text-gray-600 tracking-wide bg-slate-100">
                    <span className="bg-emerald-100 p-2 sm:p-3 rounded-lg shadow-lg">
                      {timeRangeFrom.hour}:{timeRangeFrom.minute}&nbsp;
                      <span className="text-2xs sm:text-xs opacity-70">
                        {timeRangeFrom.meridiem}
                      </span>
                    </span>
                    <span className="text-gray-400">
                      &nbsp;&nbsp;–&nbsp;&nbsp;
                    </span>
                    <span className="bg-red-100 p-2 sm:p-3 rounded-lg shadow-lg">
                      {timeRangeTo.hour}:{timeRangeTo.minute}&nbsp;
                      <span className="text-2xs sm:text-xs opacity-70">
                        {timeRangeTo.meridiem}
                      </span>
                    </span>
                  </div>
                </div>
                <div className="grid grid-cols-10 mt-2">
                  {[
                    E_TimeRangeSelectorTimeZoneListItemActions.setAsMain,
                    E_TimeRangeSelectorTimeZoneListItemActions.change,
                    E_TimeRangeSelectorTimeZoneListItemActions.remove
                  ].map((btnName) => (
                    <button
                      key={btnName}
                      className="favItemActionBtn"
                      data-time-zone-name={tzData.timeZoneName}
                      data-action-name={btnName}
                      onClick={eventHandlers.handleTimeZoneListItemAction}
                    >
                      {btnName}
                    </button>
                  ))}
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
}

export default TimeZone;
