import { DateTime } from "luxon";
import { SliderProps } from "rc-slider";
import { useEffect, useReducer, useState } from "react";

import { toNumber } from "shared/helpers/number";
import { useActiveBreakpoint } from "shared/hooks/window";
import {
  convertDateTimeValuesToDegreeValues,
  convertDegreeValuesToTimeValuesForDisplay,
  get24HourValue,
  getClockMarksResponsive,
  getTimeRangeSelectorEventHandlers,
  initialState,
  getSliderStylePropsResponsive
} from "./helpers";
import {
  E_TimeRangeSelectorActions,
  T_Degree,
  T_TimeRangeSelectorAction,
  T_TimeRangeSelectorState
} from "./types";

export function useTimeRangeSelector() {
  const [state, dispatch] = useReducer(timeRangeSelectorReducer, initialState);
  const eventHandlers = getTimeRangeSelectorEventHandlers(dispatch);

  useEffect(() => {
    const [dataOfMainTimeZone] = state.timeZonesData;
    eventHandlers.handleTimeRangeChange(
      dataOfMainTimeZone.timeRange.degreeValues
    );
  }, []);

  return {
    state,
    eventHandlers
  };
}

export function useSliderPropsResponsive(
  initialSliderProps: SliderProps,
  isTarget24HourFormat: boolean,
) {
  const activeBreakpoint = useActiveBreakpoint();
  const [sliderProps, setSliderProps] = useState({
    marks: getClockMarksResponsive(isTarget24HourFormat, activeBreakpoint),
    ...initialSliderProps
  });

  useEffect(() => {
    const newClockMarks = getClockMarksResponsive(isTarget24HourFormat, activeBreakpoint);
    const newStyleProps = getSliderStylePropsResponsive(initialSliderProps, activeBreakpoint);
    
    setSliderProps({
      marks: newClockMarks,
      ...newStyleProps
    });

  }, [isTarget24HourFormat, activeBreakpoint, initialSliderProps]);

  return sliderProps;
}

function timeRangeSelectorReducer(
  state: T_TimeRangeSelectorState,
  [type, payload]: T_TimeRangeSelectorAction
): T_TimeRangeSelectorState {
  switch (type) {
    case E_TimeRangeSelectorActions.toggle24HourFormat: {
      return {
        ...state,
        is24HourFormat: !state.is24HourFormat
      };
    }

    case E_TimeRangeSelectorActions.setMainTimeZone: {
      const newMainTimeZoneName = payload as string;
      const mainTimeZoneData = state.timeZonesData.find(
        (tzData) => tzData.timeZoneName === newMainTimeZoneName
      );
      const otherTimeZonesData = state.timeZonesData.filter(
        (tzData) => tzData.timeZoneName !== newMainTimeZoneName
      );

      if (mainTimeZoneData) {
        return {
          ...state,
          timeZonesData: [mainTimeZoneData, ...otherTimeZonesData]
        };
      }

      return state;
    }

    case E_TimeRangeSelectorActions.setTimeRangeHandle: {
      const mainTimeZoneDegreeValues = payload as T_Degree[];
      const mainTimeZoneTimeValuesForDisplay =
        convertDegreeValuesToTimeValuesForDisplay(
          mainTimeZoneDegreeValues,
          state.is24HourFormat
        );
      const [mainTimeZoneData] = state.timeZonesData;
      const newTimeZonesData = state.timeZonesData.map((tzData, index) => {
        const isTheMainTimeZone = index === 0;

        if (isTheMainTimeZone) {
          return {
            ...tzData,
            timeRange: {
              degreeValues: mainTimeZoneDegreeValues,
              timeValuesForDisplay: mainTimeZoneTimeValuesForDisplay
            }
          };
        }

        const mainTimeZoneDateTime = DateTime.local().setZone(
          mainTimeZoneData.timeZoneName
        );
        const [
          mainTimeZoneTimeValuesForDisplayFrom,
          mainTimeZoneTimeValuesForDisplayTo
        ] = mainTimeZoneTimeValuesForDisplay;
        const targetTimeZoneDateTimeFrom = mainTimeZoneDateTime
          .set({
            hour: get24HourValue(
              mainTimeZoneTimeValuesForDisplayFrom.hour,
              mainTimeZoneTimeValuesForDisplayFrom.meridiem
            ),
            minute: toNumber(mainTimeZoneTimeValuesForDisplayFrom.minute)
          })
          .setZone(tzData.timeZoneName);
        const targetTimeZoneDateTimeTo = mainTimeZoneDateTime
          .set({
            hour: get24HourValue(
              mainTimeZoneTimeValuesForDisplayTo.hour,
              mainTimeZoneTimeValuesForDisplayTo.meridiem
            ),
            minute: toNumber(mainTimeZoneTimeValuesForDisplayTo.minute)
          })
          .setZone(tzData.timeZoneName);
        const degreeValues = convertDateTimeValuesToDegreeValues([
          targetTimeZoneDateTimeFrom,
          targetTimeZoneDateTimeTo
        ]);
        const timeValuesForDisplay = convertDegreeValuesToTimeValuesForDisplay(
          degreeValues,
          state.is24HourFormat
        );

        return {
          ...tzData,
          timeRange: {
            degreeValues,
            timeValuesForDisplay
          }
        };
      });

      return {
        ...state,
        timeZonesData: newTimeZonesData
      };
    }

    default: {
      return state;
    }
  }
}
